import React, { Component } from 'react'
import { login } from '../store/actions/user.js';
import { connect } from 'react-redux';

const Login = (props) => {
    const login = () => {
        props.login('张三')
        props.history.replace(props.location.state.pathname)
    }
    return (
        <div style={{textAlign:"center"}}
        >
            <div style={{margin:'50px'}}><i className='iconfont icon-taobao' 
            style={{color: 'rgb(255, 78, 34)',
            height:'50px',
            width:'50px',
            margin:'50px',
            fontSize:'40px'}}></i></div>
            <div style={{margin:'20px'}}><input type="text" placeholder="手机号/邮箱/会员名"
            style={{height:'40px',
            width:'300px',
            border:'2px solid rgb(255, 78, 34)',
            borderRadius:'10px'}}/></div>
            <div style={{margin:'20px'}}><input type="text" placeholder="请输入登录密码"
             style={{height:'40px',
             width:'300px',
             border:'2px solid rgb(255, 78, 34)',
             borderRadius:'10px'}}/></div>
            <button style={{height:'50px',
             width:'300px',
             backgroundColor:'rgb(255, 78, 34)',
             borderRadius:'10px'}} onClick={login} >登录</button>
        </div>
    )
}


export default connect(null, { login })(Login)
